<!--
    This component serves to generate an input form to enter parameters.
    A abi file is used to generate the form

    props:
        inputs: Array
-->

<template>

    <transition name="anim" appear mode="out-in">
    <v-form v-if="show" ref="form" v-model="valid">
        <v-text-field
            v-for="f in textFields"
            :key="f.name"
            :label="f.name | filterLabel"
            :hint="f.type | filterType"
            :counter="f.counter"
            v-model="f.value"
            :rules="f.rules"
            required>
            <template v-slot:prepend>
                <v-tooltip v-if="f.type === 'address'" color="info" bottom>
                    <template v-slot:activator="{ on, attrs }">
                        <v-icon v-bind="attrs" v-on="on" @click="insertWallet(f)">mdi-wallet-outline</v-icon>
                    </template>
                    <span>Insert my wallet address</span>
                </v-tooltip>
            </template>
        </v-text-field>

        <v-card v-for="a in formArrays" :key="a.name" :style="cssVars">
            <v-card-subtitle style="padding-bottom: 0px">{{ a.name | filterLabel}}</v-card-subtitle>
            <v-card-text>
                <transition-group name="anim" appear>
                    <v-text-field
                        v-for="(v, i) in a.value"
                        :key="i"
                        v-model="a.value[i]"
                        :counter="a.counter"
                        :rules="a.rules"
                        required>
                        <template v-slot:prepend>
                            <v-tooltip v-if="a.type === 'address'" color="info" bottom>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-icon v-bind="attrs" v-on="on" @click="insertWallet(f)">mdi-wallet-outline</v-icon>
                                </template>
                                <span>Insert my wallet address</span>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </transition-group>

                <div class="form-buttons">
                    <v-btn @click="addArrayInput(a.name)" color="secondary darken-2" class="mt-1" dark fab small>
                        <v-icon>mdi-plus</v-icon>
                    </v-btn>
                </div>
            </v-card-text>
        </v-card>

        <v-checkbox
            v-for="b in checkBoxes"
            :key="b.name"
            :label="b.name | filterLabel"
            v-model="b.value">
        </v-checkbox>
    </v-form>
    </transition>

</template>

<script>

    import { mapState } from 'vuex';

    export default {

        name: 'AbiForm',

        props: {
            inputs: Array
        },

        data () {
            return {
                show: false,
                valid: false,
                form: []
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3,
                'wallet': state => state.chain.wallet
            }),

            textFields() {
                return this.form.filter( obj => !obj.check && !obj.array );
            },

            formArrays() {
                return this.form.filter( obj => obj.array );
            },

            checkBoxes() {
                return this.form.filter( obj => obj.check );
            },

            cssVars() {
                return {
                    '--form-border-color': this.$vuetify.theme.dark ? '#ffffffaf' : '#000000af'
                };
            }
        },

        watch: {
            inputs: {
                handler(val) {
                    let noSleep = false;
                    if (!this.show)
                        noSleep = true;
                    
                    this.show = false;
                    this.form = [];

                    val.forEach( i => {
                        const obj = {
                            name: i.name,
                            value: i.type.endsWith('[]') ? [''] : '',
                            type: i.type,
                            check: i.type === 'bool',
                            array: i.type.endsWith('[]'),
                            counter: 0,
                            rules: [
                                v => !!v || 'Required'
                            ]
                        }

                        if (i.type.startsWith('int') || i.type.startsWith('uint')) {
                            obj.rules.push( v => /^[0-9]*$/.test(v) || 'Must be an integer number' );
                        } else if (i.type === 'address') {
                            obj.rules.push( v => this.w3.utils.isAddress(v) || 'Must be valid Ethereum address' );
                        } else if (i.type.startsWith('bytes')) {
                            const len = parseInt(i.type.slice(5));
                            obj.counter = len;
                            obj.rules.push( v => v !== null && v.length <= len || `Input longer than ${len} characters` );
                        } else if (i.type.startsWith('Ether')) {
                            obj.rules.push( v => /^\d*(\.\d*)?$/.test(v) || 'Must be a number' );
                        }

                        this.form.push(obj);
                    });

                    if (noSleep) {
                        this.show = true;
                    } else {
                        this.sleep(500).then( () => this.show = true );
                    }
                    
                },

                immediate: true
            }
        },

        filters: {
            filterLabel(value) {
                const val = value.replace('_', '');
                return (val.charAt(0).toUpperCase() + val.slice(1)).split(/(?=[A-Z,0-9])/).join(' ');
            },

            filterType(value) {
                if (value.endsWith('[]')) {
                    return value.slice(0, -2);
                } else {
                    return value;
                }
            }
        },

        methods: {

            reset() {
                this.$refs.form.reset();
                this.valid = false;
            },

            validate() {
                return this.$refs.form.validate();
            },

            addArrayInput(name) {
                const field = this.form.filter( o => o.name === name )[0];
                field.value.push('');
            },

            insertWallet(e) {
                e.value = this.wallet;
            },

            sleep(ms) {
                return new Promise(resolve => setTimeout(resolve, ms));
            },
        }
    }

</script>

<style scoped>

    .form-buttons {
        display: flex;
        justify-content: right;
    }

    .v-form .v-card {
        box-shadow: none !important;
        margin-bottom: 15px;
        margin-top: 15px;
        border-style: solid;
        border-width: 1px;
        border-radius: 10px;
        border-color: var(--form-border-color);
    }

    .anim-enter-active,
    .anim-leave-active {
        transition: all .5s ease-in-out;
    }

    /* .anim-enter-active {
        transition-delay: 5s;
    } */

    .anim-enter,
    .anim-leave-to {
        opacity: 0;
        transform: translateY(-30px);
    }

</style>
